<template>
  
    <div class="login">
      <div class="btnWrapper">
        <div class="loginButton" @click="moveRegisterForm">登录</div>
        <div class="registerButton" @click="moveLoginForm">注册</div>
      </div>
      <form class="loginForm" v-if="loginActive">
        <p>{{this.msg}}</p>
      	<input type="text" v-model="loginModel.id" class="input" placeholder="账号"></input>
      	<input type="password" v-model="loginModel.password" class="input" placeholder="密码"></input>
      	<div>
          <input type="button" value="确认" @click="login" class="confirmButton"> 
        </div>       
      </form>
      <form class="registerForm" v-if="!loginActive">
        <p>{{this.msg}}</p>
        <input type="text" v-model="registerModel.id" class="input" placeholder="账号"></input>
        <input type="password" v-model="registerModel.password" class="input" placeholder="密码"></input>
        <input type="password" v-model="registerModel.confirmPassword" class="input" placeholder="确认"></input>
        <div>
          <input type="button" value="提交" @click="register" class="confirmButton">   
        </div>     
      </form>
    </div>

</template>

<script>
import { bus } from '../../../assets/bus.js'
export default {
  data(){
  	return{
      loginUrl:"/dachuang/Login",
      registerUrl:"/dachuang/Register",
      loginModel:{
        "id":"",
        "password":""
      },
      registerModel:{
        "id":"",
        "password":"",
        "confirmPassword":""
      },
      loginActive:true,
      message:""
    }
  },
  methods:{
    register:function(){
      var vm=this;
      var model=this.registerModel;
      if(!this.isModelEmpty(model)){
        vm.$http.post(vm.registerUrl,vm.registerModel,{
                        emulateJSON:true
                      }).then(function(res){
                         console.log(res.body);
                        if(res.body.code==1){
                          vm.msg="注册成功";
                          console.log(res.body);
                        }else{
                          vm.msg=res.body.message;
                        }
                      })
       }else{
        vm.msg="账号密码不能为空"
       }
    },
  	login:function(){
  		var vm=this;
      var model=this.loginModel
      if(!this.isModelEmpty(model)){
        vm.$http.post(vm.loginUrl,vm.loginModel,{
                        emulateJSON:true
                      }).then(function(res){
                         console.log(res.body);
                        if(res.body.code==1){
                          vm.msg="登录成功";               
                          var user=res.body.data.id;
                          console.log(res.body);
                          sessionStorage.user=user;
                          console.log("用户登录成功"); 
                          this.$router.push("/sale");

                        }else{
                          vm.msg=res.body.message;
                        }
                      })
       }else{
        vm.msg="账号密码不能为空"
       }
  	},
    isModelEmpty:function(model){
      if(model.id==""||model.password==""){
        return true;
      }else{
        return false;
      }
    },
    moveLoginForm:function(){
      this.loginActive=false;
    },
    moveRegisterForm:function(){
      this.loginActive=true;
    }


  }

       
}
</script>

<style scoped>
  button{
    cursor: pointer;
  }
  .login{
    width: 281px;
    height: 267px;
    position: relative;
    display: inline-block;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.2);

  }
  .loginForm{
    width: 281px;
    height: 219px;
    display: inline-block;
    position: absolute;
    left: 0px;
    top: 48px;
    background: rgba(201, 201, 201, 0.4);
    border-radius: 0 0 20px 20px;
    animation: myfirst 1s;
  }
   .registerForm{
    width: 281px;
    height: 219px;
    display: inline-block;
    position: absolute;
    left: 0px;
    top: 48px;
    background: rgba(134, 134, 134, 0.4);
    border-radius: 0 0 20px 20px;
    animation: myfirst1 1s;
    animation-out:myfirst1 1s;
  }
  @keyframes myfirst
  {
  from {left:-282px;}
  to {left: 0px;}
  }
  @keyframes myfirst1
  {
  from {left:282px;}
  to {left: 0px;}
  }
  .loginFormActive{
    animation: myfirst 2s;
  }
  .btnWrapper{
    display: flex;
    height: 48px;
    line-height: 48px;
    background: rgba(201, 201, 201, 0.3);
    border-radius: 20px 20px 0 0;
   }
  .loginButton{
    height: 48px;
    flex:2;
  }
  .registerButton{
    height: 48px;
    color: #fff;
    flex:1;
  }
  .loginButton:hover{
    flex:2;
    transition:0.6s all;
    cursor: pointer;
  }
  .registerButton:hover{
    flex:4;
    transition:0.6s all;
    cursor: pointer
  }
  .input{
    height: 20px;
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.247058823529412);
    font-family: 'Arial Normal', 'Arial';
    font-weight: 400;
    font-style: normal;
    font-size: 13px;
    text-decoration: none;
    color: #000000;
    background: #fff;
    text-align: left;
    border-color: transparent;
    outline-style: none;

  }
  .confirmButton{
    width: 135px;
    height: 30px;
    margin: 10px;
    color: #fff;
    background: inherit;
    background-color: rgba(253, 208, 72, 0.847058823529412);
    border: none;
    border-radius: 5px;
   }
   .confirmButton:hover{
    cursor: pointer
   }  
  .registerButton{
    background: rgba(134, 134, 134, 1);
  }
  
</style>


